'use client'; 

import { useState } from 'react';

const API_URL = 'http://localhost:8000';

// --- Seletor de M칡s (sem altera칞칚o) ---
function MonthSelector({ currentDate, onDateChange }) {
  const handlePreviousMonth = () => { const newDate = new Date(currentDate); newDate.setMonth(newDate.getMonth() - 1); onDateChange(newDate); };
  const handleNextMonth = () => { const newDate = new Date(currentDate); newDate.setMonth(newDate.getMonth() + 1); onDateChange(newDate); };
  const formattedDate = currentDate.toLocaleString('pt-BR', { month: 'long', year: 'numeric' }).replace(/^\w/, (c) => c.toUpperCase());
  return (
    <div className="flex justify-between items-center mb-8 bg-fin-card/30 p-4 rounded-2xl border border-white/5 backdrop-blur-sm shadow-inner">
      <button onClick={handlePreviousMonth} className="px-3 py-2 text-fin-gold border border-fin-gold/40 rounded-lg hover:bg-fin-gold hover:text-black transition-all">&lt; Anterior</button>
      <h2 className="text-2xl font-light text-white tracking-wide">{formattedDate}</h2>
      <button onClick={handleNextMonth} className="px-3 py-2 text-fin-gold border border-fin-gold/40 rounded-lg hover:bg-fin-gold hover:text-black transition-all">Pr칩ximo &gt;</button>
    </div>
  );
}

// --- Componente de Checkbox (sem altera칞칚o) ---
function Checkbox({ label, checked, onChange, id }) {
  return (
    <label htmlFor={id} className="flex items-center gap-3 p-4 bg-fin-dark/50 rounded-xl border border-white/10 cursor-pointer hover:bg-fin-dark transition-all">
      <input id={id} type="checkbox" checked={checked} onChange={onChange} name={id} className="h-5 w-5 rounded-md bg-fin-dark/80 border-2 border-fin-gold/50 text-fin-gold focus:ring-fin-gold/50"/>
      <span className="text-white font-medium">{label}</span>
    </label>
  );
}

// --- Componente Principal (ATUALIZADO) ---
export default function ReportsPage() {
  const [currentDate, setCurrentDate] = useState(new Date());
  const [isLoading, setIsLoading] = useState(false);
  
  const [exportOptions, setExportOptions] = useState({
    include_income: true,
    include_expenses: true,
    include_goals: true,
    include_portfolio: true,
    include_credit_cards: true,
    include_budget: true,
    include_balance: true,
  });

  const handleCheckboxChange = (event) => {
    const { name, checked } = event.target;
    setExportOptions(prev => ({ ...prev, [name]: checked }));
  };

  // 游녢 ATUALIZADO: L칩gica para lidar com PDF ou Excel
  const handleDownload = (format) => {
    setIsLoading(true);
    
    const month = currentDate.getMonth() + 1;
    const year = currentDate.getFullYear();
    const params = new URLSearchParams({ month, year, ...exportOptions });
    
    const endpoint = format === 'pdf' ? '/pdf' : '/excel';
    const downloadUrl = `${API_URL}/reports${endpoint}?${params.toString()}`;

    // Para PDF, podemos abrir em nova aba. Para Excel, for칞amos o download.
    if (format === 'pdf') {
      window.open(downloadUrl, '_blank');
      setIsLoading(false);
    } else {
      // Para for칞ar o download do Excel, criamos um link 'a' invis칤vel
      axios({
        url: downloadUrl,
        method: 'GET',
        responseType: 'blob', // Importante para arquivos
      }).then((response) => {
         const href = URL.createObjectURL(response.data);
         const link = document.createElement('a');
         link.href = href;
         link.setAttribute('download', `relatorio_${year}_${month}.xlsx`);
         document.body.appendChild(link);
         link.click();
         document.body.removeChild(link);
         URL.revokeObjectURL(href);
         setIsLoading(false);
      }).catch(error => {
        console.error("Erro no download:", error);
        setIsLoading(false);
      });
    }
  };

  return (
    <div className="max-w-4xl mx-auto">
      <div className="bg-gradient-to-br from-fin-card to-fin-dark/80 p-8 rounded-3xl shadow-2xl mb-8 border border-fin-gold/20 backdrop-blur-sm">
        
        <div className="flex items-center gap-3 mb-8">
          <div className="p-3 bg-fin-gold/20 rounded-2xl"><span className="text-2xl">游늯</span></div>
          <div>
            <h2 className="text-2xl font-bold text-white">Exportar Relat칩rios</h2>
            <p className="text-fin-gold/70 text-sm">Gere um arquivo com os dados do per칤odo</p>
          </div>
        </div>

        <MonthSelector currentDate={currentDate} onDateChange={setCurrentDate} />

        <div className="mb-8">
          <h3 className="text-lg font-semibold text-white mb-4">Selecione os dados para incluir:</h3>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <Checkbox label="Entradas (Receitas)" id="include_income" checked={exportOptions.include_income} onChange={handleCheckboxChange} />
            <Checkbox label="Sa칤das (Despesas)" id="include_expenses" checked={exportOptions.include_expenses} onChange={handleCheckboxChange} />
            <Checkbox label="Status das Metas" id="include_goals" checked={exportOptions.include_goals} onChange={handleCheckboxChange} />
            <Checkbox label="Resumo do Portf칩lio" id="include_portfolio" checked={exportOptions.include_portfolio} onChange={handleCheckboxChange} />
            <Checkbox label="Gastos no Cart칚o" id="include_credit_cards" checked={exportOptions.include_credit_cards} onChange={handleCheckboxChange} />
            <Checkbox label="Resumo do Or칞amento" id="include_budget" checked={exportOptions.include_budget} onChange={handleCheckboxChange} />
            <Checkbox label="Balan칞o de Caixa" id="include_balance" checked={exportOptions.include_balance} onChange={handleCheckboxChange} />
          </div>
        </div>

        {/* 游녢 BOT칏ES ATUALIZADOS 游녢 */}
        <div className="flex flex-col md:flex-row gap-4">
          <button 
            onClick={() => handleDownload('pdf')}
            disabled={isLoading}
            className="w-full bg-gradient-to-r from-fin-red to-rose-700 hover:opacity-90 text-white font-bold py-4 px-6 rounded-xl transition-all shadow-lg disabled:opacity-50"
          >
            {isLoading ? "Gerando..." : "Gerar Relat칩rio PDF"}
          </button>
          <button 
            onClick={() => handleDownload('excel')}
            disabled={isLoading}
            className="w-full bg-gradient-to-r from-green-600 to-emerald-700 hover:opacity-90 text-white font-bold py-4 px-6 rounded-xl transition-all shadow-lg disabled:opacity-50"
          >
            {isLoading ? "Gerando..." : "Exportar para Excel (.xlsx)"}
          </button>
        </div>

      </div>
    </div>
  );
}